<template>
  <div id="loader">
    <div class="text-center">
      <p> ...</p>
      <p>  ?
        <a href="#" v-on:click="logout()"></a>   .
      </p>
      <p class="text-danger">  -     vk.com     .
      </p>
      <p class="text-danger">    -     (/-).  .</p>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    logout() {
      this.$cookies.remove("accessToken");
      window.location.replace("/");
    }
  }
};
</script>



